import React from 'react';
import { Card, Typography, Button, ButtonBase, CardContent, CardMedia } from '@material-ui/core';
import Page from '../../reducers/pages/page';
import useStyles from './styles';
import { Link } from 'react-router-dom';

interface AddPageProps {
    weekId : number;
    onAdd : () => void;
}

export function AddPageCard(props : AddPageProps) {
    const classes = useStyles();
    return (
        <Card className={classes.pageCard}>
            <Button 
                className={classes.pageButton}
                onClick={props.onAdd}
            >
                +
            </Button>
        </Card>
    )
}

export default function PageCard(props : Page) {
    const classes = useStyles();
    return(
        <Card className={classes.pageCard}>
            <ButtonBase className={classes.cardButton} onClick={() => window.open(`https://www.homestuck.com/story/${props.pageNumber}`)}>
                <CardContent className={classes.cardContent}>
                    <Typography className={classes.title} variant='h6'>#{props.pageNumber}</Typography>
                </CardContent>
                <CardMedia 
                    className={classes.cardMedia}
                    component='img'
                    title={props.pageNumber.toString()}
                    src={props.pictureSrc}
                />
                <CardContent className={classes.cardContent}>
                    <Typography className={classes.description} variant='subtitle1'>{props.description}</Typography>
                </CardContent>
            </ButtonBase>
        </Card>
    );
}