import api from '../api';
import { Dispatch } from 'redux';
import { GET_PEOPLE_START, GET_PEOPLE_ERROR, GET_PEOPLE_SUCCESS } from '../../reducers/people/actions';
import { Person } from '../../components/personselect';

const start = {
    type : GET_PEOPLE_START
};

const fail = {
    type : GET_PEOPLE_ERROR,
};

function success(people : Person[]) {
    return {
        type : GET_PEOPLE_SUCCESS,
        data : people
    }
};

export default function GetPeople() {
    return function(dispatch : Dispatch) {
        dispatch(start);
        return api.get(`/people`)
            .then(response => dispatch(success(response.data as Person[])))
            .catch(err => dispatch(fail));
    }
}