import { Person } from "../../components/personselect";

export const GET_PEOPLE_START = 'GET_PEOPLE_START';
export const GET_PEOPLE_SUCCESS = 'GET_PEOPLE_SUCCESS';
export const GET_PEOPLE_ERROR = 'GET_PEOPLE_ERROR';

export interface PeopleStartAction {
    type : typeof GET_PEOPLE_START;
}

export interface PeopleSuccessAction {
    type : typeof GET_PEOPLE_SUCCESS;
    data : Person[];
}

export interface PeopleErrorAction {
    type : typeof GET_PEOPLE_ERROR;
}

type PeopleAction = PeopleStartAction | PeopleSuccessAction | PeopleErrorAction;

export default PeopleAction;