import React, { useState } from 'react';
import { Grid, Typography, Button, Input } from '@material-ui/core';
import useStyles from './styles';

export interface NewPageProps {
    onAccept : (pageNumber : number, description : string, pictureSrc : string) => void;
}

export default function NewPage(props : NewPageProps) {
    const classes = useStyles();
    const [pageNumber, setPageNumber] = useState(0);
    const [description, setDescription] = useState('');
    const [pictureSrc, setPictureSrc] = useState('');
    return(
        <div className={classes.modalContent}>
            <Grid className={classes.modalGrid} container>
                <Grid item xs={12}><Typography variant='h4'>Add New Page</Typography></Grid>
                <Grid item xs={6}><label>Page Number</label></Grid>
                <Grid item xs={6}><Input type='number' value={pageNumber} onChange={e => setPageNumber(parseInt(e.target.value))} /></Grid>
                <Grid item xs={6}><label>Description</label></Grid>
                <Grid item xs={6}><Input type='text' value={description} onChange={e => setDescription(e.target.value)} /></Grid>
                <Grid item xs={6}><label>Image Link</label></Grid>
                <Grid item xs={6}><Input type='text' value={pictureSrc} onChange={e => setPictureSrc(e.target.value)} /></Grid>
            </Grid>
            <Button
                onClick={() => props.onAccept(pageNumber, description, pictureSrc)}
                variant='contained'
                color='primary'
            >
                Add
            </Button>
        </div>
    );
}