import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { selectPages } from '../../reducers/pages';
import GetPages from '../../api/pages/get';
import { CircularProgress, Grid, Modal, Typography, Button } from '@material-ui/core';
import PageCard, { AddPageCard } from './pagecard';
import useStyles from './styles';
import NewPage from './newpage';
import AddPage from '../../api/pages/add';

export interface PagesProps {
    weekId : number;
}

export default function PagesDisplay(props : PagesProps) {
    const allPages = useSelector(selectPages);
    const [modalOpen, setModalOpen] = useState(false);
    const pageStatus = allPages[props.weekId];
    const dispatch = useDispatch();
    const classes = useStyles();

    const children = [];

    if (pageStatus === undefined) {
        GetPages(props.weekId)(dispatch);
    } else {
        switch (pageStatus.status) {
            case 'unloaded':
                GetPages(props.weekId)(dispatch);
                break;
            case 'loading':
                children.push(<Grid item xs={12}><CircularProgress /></Grid>);
                break;
            case 'loaded':
                pageStatus.pages.forEach(page => {
                    children.push(<Grid item xs={4}><PageCard {...page} /></Grid>);
                });
                children.push(<Grid item xs={4}><AddPageCard weekId={props.weekId} onAdd={() => setModalOpen(true)}/></Grid>);
                break;
            case 'errored':
                children.push(<Grid item xs={12}><span>Error</span></Grid>);
                break;
        }
    }

    function addPage(pageNumber : number, description : string, pictureSrc : string) {
        setModalOpen(false);
        AddPage(props.weekId, { pageNumber, description, pictureSrc })(dispatch);
    }

    return (
        <>
            <Grid container spacing={3} justify='space-evenly'>
                {children}
            </Grid>
            <Modal
                className={classes.modal}
                open={modalOpen}
                onClose={() => setModalOpen(false)}
            >
                <NewPage onAccept={addPage} />
            </Modal>
        </>
    );
}