import { PeopleState } from "./state";
import PeopleAction from "./actions";
import { RootState } from "..";

const defaultState : PeopleState = {
    people : [],
    status : 'unloaded',
}

export const selectPeople = (state : RootState) => state.people;

export default function PeopleReducer(state : PeopleState = defaultState, action : PeopleAction) : PeopleState {
    switch (action.type) {
        case 'GET_PEOPLE_START':
            return {
                people : [],
                status : 'loading'
            };
        case 'GET_PEOPLE_SUCCESS':
            return {
                people: action.data,
                status: 'loaded'
            };
        case 'GET_PEOPLE_ERROR':
            return {
                people : [],
                status: 'errored'
            }
        default:
            return state;
    }
}