import React from 'react';
import { Select, FormControl, InputLabel, MenuItem } from '@material-ui/core';
import { Colour } from '../../consts/colours';
import useStyles from './styles';
import { selectPeople } from '../../reducers/people';
import { useSelector, useDispatch } from 'react-redux';
import GetPeople from '../../api/people/get';

export interface Person {
    id : number
    name : string;
    colour : Colour;
}

export interface PersonSelectProps {
    id : string;
    selected : Person | undefined;
    setSelected : (p : Person) => void;
}

export default function PersonSelect(props : PersonSelectProps) {
    const classes = useStyles();
    const peopleState = useSelector(selectPeople);
    const dispatch = useDispatch();

    if (peopleState.status === 'unloaded') {
        GetPeople()(dispatch);
    }

    return(
        <FormControl className={classes.root} disabled={props.selected !== undefined}>
            <InputLabel
                className={classes.label}
                htmlFor={props.id}
            >
                User
            </InputLabel>
            <Select
                inputProps={{
                    name: 'user',
                    id: props.id,
                    classes: { icon: classes.icon}
                }}
                value={props.selected?.name}
                onChange={e => props.setSelected(peopleState.people.filter(a => a.name === e.target.value)[0])}
                className={classes.select}
                style={{color: props.selected !== undefined ? props.selected.colour : 'black'}}
            >
                {
                    peopleState.people.map(option =>
                        <MenuItem
                            className={classes.item}
                            style={{color: option.colour}}
                            value={option.name}
                        >
                            {option.name}
                        </MenuItem>
                    )
                }
            </Select>
        </FormControl>
    );
}